.footer {
  max-width: 880px;
  margin: 0 auto;
  padding: 0;
  padding: 66px 0 60px;
}

@media screen and (max-width: 920px) {
  .footer {
    max-width: 581px;
  }
}

@media screen and (max-width: 600px) {
  .footer {
    max-width: 282px;
    padding: 48px 0 36px;

    font-size: 14px;
  }
}
